<template>
  <div class="main">
    <div class="retbtn" @click="retClick">
      <span class="iconfont icon-zuo"></span>
    </div>

    <div class="address" @click="gotoAddress">
      <div class="iconleft">
        <span class="iconfont icon-ditu_dingwei_o"></span>
      </div>

      <div class="address-text">
        <div class="name">
          {{ choose?.name }} <span class="phone">{{ choose?.tel }}</span>
        </div>
        <div class="detail">
          {{
            choose
              ? choose?.province +
                choose?.city +
                choose?.county +
                choose?.addressDetail
              : "管理您的收货地址"
          }}
        </div>
      </div>
      <div class="iconyou">
        <span class="iconfont icon-you"></span>
      </div>
    </div>
    <div class="goodslist">
      <van-card
        v-for="item in payList"
        :key="item.id"
        :num="item.num"
        :price="item.price.toFixed(2)"
        :desc="item.colorname + item.ggname"
        :title="item.goodsname"
        :thumb="item.img"
      />
    </div>
    <van-submit-bar
      :price="payPrice"
      button-text="提交订单"
      @submit="onSubmit"
    />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      address: {},
      choose: null,
    };
  },
  methods: {
    ...mapActions("address", ["initList"]),
    retClick() {
      this.$router.push("/shoppingcar");
    },
    onSubmit() {},
    gotoAddress() {
      this.$router.push("/address");
    },
  },
  computed: {
    ...mapState("shoppingcar", ["list"]),
    ...mapState("address", { chooseAddress: "choose" }),
    payList() {
      return this.list.filter((item) => item.checked);
    },
    payPrice() {
      return this.payList.length
        ? this.payList
            .map((item) => item.price * item.num)
            .reduce((a, b) => a + b) * 100
        : 0;
    },
  },
  async created() {
    if (!this.chooseAddress) {
      await this.initList(this.$store.state.userinfo.id);
    }
    this.choose = this.chooseAddress;
  },
};
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}
.goodslist{
  margin-bottom: 50px;
}
.retbtn {
  position: fixed;
  top: 15px;
  left: 15px;
  width: 30px;
  height: 30px;
  background: rgba($color: #999, $alpha: 0.8);
  text-align: center;
  color: #fff;
  line-height: 30px;
  font-size: 14px;
  border-radius: 15px;
  .icon-zuo {
    padding-left: 7px;
    font-size: 18px;
  }
}
.address {
  width: 100%;
  min-height: 60px;
  background-color: #fff;
  margin-bottom: 10px;
  display: flex;

  .iconleft {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70px;
    .icon-ditu_dingwei_o {
      font-size: 36px;
      color: #6c6c6c;
    }
  }
  .iconyou {
    display: flex;
    align-items: center;
    width: 30px;
    .icon-you {
      font-size: 18px;
      color: #999;
      font-weight: 600;
    }
  }
  .address-text {
    flex: 1;
    padding: 12px 0 8px 0;
    overflow: hidden;
    .name {
      width: 100%;
      height: 18px;
      font-size: 16px;
      line-height: 18px;
      color: #363131;
      font-weight: bold;
      display: flex;
      .phone {
        flex: 1;
        margin-left: 16px;
        overflow: hidden;
      }
    }
    .detail {
      width: 100%;
      line-height: 18px;
      margin-top: 6px;
      color: #6c6c6c;
      font-size: 14px;
    }
  }
}
</style>